:root {
  --font-body: "Montserrat", sans-serif;
  --font-thin: 300;
  --font-medium: 500;
  --font-bold: 700;
  --font-extra-bold: 800;
  --title-size: 2.5rem;
  --subtitle-size: 1.5rem;
  --body-size: 1rem;
}

:root {
  --primary-color: #370665;
  --secondary-color: #916bbf;
  --tertiary-color: #f3f1f5;
  --font-color: #272727;
  --bg-color: #fff;
  --disabled-color: #807e81;
  --error-color: #f05454;
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after,
h1 {
  box-sizing: inherit;
  margin: 0;
  padding: 0;
}

button {
  border: none;
  outline: none;
  cursor: pointer;
}

@keyframes flickering {
  100% {
    transform: scale(1.5);
  }
}
body {
  font-family: var(--font-body);
}

h1,
h2,
h3 {
  margin-top: 0;
  line-height: 1.1;
}

a,
a:visited,
a:active {
  text-decoration: none;
}

.container {
  padding: 0 1.5rem;
  max-width: 69.375rem;
  margin: 0 auto;
}
@media (min-width: 71.875em) {
  .container {
    padding: 0;
    transform: translate(0, 0);
  }
}

*::selection {
  background: var(--secondary-color);
}

html {
  scroll-behavior: smooth;
}

@media (max-width: 54.75rem) {
  html {
    font-size: 12px;
  }
}
:root {
  --margin-section: 5rem;
}

.modal-container {
  position: fixed;
  width: 100%;
  height: 100vh;
  left: 0;
  top: 0;
  z-index: 100;
  display: none;
  justify-content: center;
  align-items: center;
  background: radial-gradient(var(--tertiary-color) 50%, transparent);
}

.modal {
  margin: 0 3rem;
  padding: 2rem;
  max-width: 43.75rem;
  background: var(--bg-color);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 1rem;
  border-radius: 10px;
  box-shadow: 0 0 40px -25px var(--font-color);
}
.modal__img img {
  width: 100%;
  height: 100%;
}
.modal__title, .modal__content {
  text-align: center;
}
.modal__content {
  max-width: 80%;
}
.modal__btn {
  padding: 1rem 2rem;
  background: var(--primary-color);
  border: none;
  cursor: pointer;
  border-radius: 1rem;
  color: var(--tertiary-color);
  font-weight: var(--font-bold);
  transition: background 200ms ease;
}
.modal__btn:active {
  background: var(--secondary-color);
}

.app-container {
  max-width: 1420px;
  margin: 0 auto;
  background: var(--bg-color);
}

.logo {
  color: var(--primary-color);
  background: var(--tertiary-color);
  padding: 1rem;
  border-radius: 100%;
}

.nav__link {
  color: var(--tertiary-color);
  font-weight: var(--font-bold);
  font-size: 1.5rem;
  padding: 0.5rem;
  transition: background 300ms ease-in-out;
}
.nav__link:hover {
  background: var(--secondary-color);
}

.header {
  position: relative;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1rem 5rem;
  background: var(--primary-color);
}
.header::after {
  content: "";
  position: absolute;
  bottom: -100%;
  left: 0;
  display: block;
  width: 100%;
  height: 100%;
  background: url("./assets/img/waves.svg") no-repeat;
  background-size: 100%;
}

.hero-texts__name {
  font-size: 7rem;
  font-weight: var(--font-extra-bold);
  color: var(--font-color);
  line-height: 7rem;
}
@media (max-width: 54.75rem) {
  .hero-texts__name {
    text-align: center;
  }
}
.hero-texts__description {
  font-size: 1.5rem;
  font-weight: var(--font-medium);
  color: var(--disabled-color);
  line-height: 1.5rem;
  margin: 2rem 0;
}
.hero-texts__description-bold {
  font-weight: var(--font-extra-bold);
  color: var(--primary-color);
}
@media (max-width: 54.75rem) {
  .hero-texts__description {
    text-align: center;
    margin: 2rem 0;
  }
}
.hero-texts__contact-btn {
  padding: 1rem 2rem;
  background: var(--primary-color);
  border: none;
  cursor: pointer;
  border-radius: 1rem;
  color: var(--tertiary-color);
  font-weight: var(--font-bold);
  transition: background 200ms ease;
}
.hero-texts__contact-btn:active {
  background: var(--secondary-color);
}
@media (max-width: 54.75rem) {
  .hero-texts__contact-btn {
    margin: 0 auto;
  }
}

:root {
  --photo-size: 20rem;
}

.hero-photo {
  width: var(--photo-size);
  height: var(--photo-size);
  outline: 1rem solid var(--primary-color);
  border-radius: 10rem;
  overflow: hidden;
}
.hero-photo img {
  width: 100%;
  object-fit: cover;
  filter: grayscale(100%);
}

.hero-section {
  padding: 5rem 0;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 8rem;
  background-color: var(--tertiary-color);
}
@media (max-width: 54.75rem) {
  .hero-section {
    flex-direction: column;
    flex-flow: column-reverse;
    gap: 2rem;
  }
}

.footer {
  position: relative;
  padding: 2rem;
  background: var(--primary-color);
  display: flex;
  justify-content: center;
  align-items: center;
}
.footer::before {
  content: "";
  position: absolute;
  top: -99%;
  left: 0;
  display: block;
  width: 100%;
  height: 100%;
  transform: rotate(180deg);
  background: url("./assets/img/waves.svg") no-repeat;
  background-size: 100%;
}
.footer__content {
  color: var(--tertiary-color);
  font-weight: var(--font-bold);
  font-size: 1.5rem;
}

.proyects {
  max-width: 80%;
  margin: 0 auto;
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(25rem, 1fr));
  gap: 2rem;
}
@media (max-width: 39.9375em) {
  .proyects {
    max-width: 100%;
  }
}

.proyects__proyect {
  position: relative;
  background: var(--tertiary-color);
  border-radius: 10px;
  padding: 2rem 2.5rem;
  cursor: pointer;
  aspect-ratio: 16/9;
}
.proyects__proyect:hover > img {
  transform: scale(1.08);
}
.proyects__proyect img {
  width: 100%;
  height: 100%;
  border-radius: 10px;
  transition: transform 200ms ease;
}
@media (max-width: 39.9375em) {
  .proyects__proyect {
    border-radius: 0;
  }
}

.proyects-section {
  margin: var(--margin-section) 0;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.proyects-section__title, .proyects-section__subtitle {
  text-align: center;
}
.proyects-section__title {
  position: relative;
  font-weight: var(--font-bold);
  font-size: var(--title-size);
  z-index: 20;
}
.proyects-section__title::before {
  position: absolute;
  left: -5%;
  top: -5%;
  z-index: -1;
  content: "";
  width: 1.875rem;
  height: 1.875rem;
  border-radius: 100%;
  background: var(--secondary-color);
  animation: flickering 900ms ease-in-out infinite alternate;
}
.proyects-section__subtitle {
  font-weight: var(--subtitle-size);
  font-size: 1.5rem;
  margin-bottom: 2rem;
}

.contact-form {
  width: 100%;
  max-width: 700px;
  font-size: var(--body-size);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 2.5rem;
  box-shadow: 0 0 30px 0px var(--tertiary-color);
  padding: 2.5rem;
  border-radius: 10px;
}
.contact-form .textarea-wrapper {
  height: 200px;
}
.contact-form .textarea-wrapper label {
  transform: translateY(-320%);
}
.contact-form .textarea-wrapper textarea {
  padding-top: 1rem;
}
.contact-form__input-wrapper {
  width: 100%;
  height: 40px;
  position: relative;
}
.contact-form__input-wrapper .input {
  border-radius: 5px;
  border: none;
  outline: none;
  position: absolute;
  left: 0;
  top: 0;
  height: 100%;
  width: 100%;
  z-index: 10;
  padding-left: 1rem;
  border: 3px solid var(--disabled-color);
  transition: border 600ms ease;
}
.contact-form__input-wrapper .input:focus {
  border: 3px solid var(--primary-color);
}
.contact-form__input-wrapper .input:focus + label {
  font-weight: var(--font-extra-bold);
  color: var(--primary-color);
}
.contact-form__input-wrapper .input:focus:required:invalid {
  border: 3px solid var(--error-color);
}
.contact-form__input-wrapper .input:focus:required:invalid + label {
  color: var(--error-color);
}
.contact-form__input-wrapper label {
  transform: translateY(-120%);
  position: absolute;
  z-index: 20;
  top: 25%;
  left: 1rem;
  font-size: 16px;
  transition: color 200ms ease, font-weight 200ms ease;
  background: var(--bg-color);
  padding: 0 1rem;
  margin-bottom: 1rem;
}
.contact-form__submit-btn {
  padding: 1rem 2rem;
  background: var(--primary-color);
  border: none;
  cursor: pointer;
  border-radius: 1rem;
  color: var(--tertiary-color);
  font-weight: var(--font-bold);
  transition: background 200ms ease;
}
.contact-form__submit-btn:active {
  background: var(--secondary-color);
}

.contact-section {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  gap: 2rem;
  margin: var(--margin-section) 0;
}
.contact-section__title {
  font-weight: var(--font-bold);
  font-size: var(--title-size);
}